.cloudinary {

	&-widget {
		height: 100%;

		&-wrapper {
			height: 100%;
			overflow: hidden;
			background-image: url(data:image/svg+xml;base64,);
			background-repeat: no-repeat;
			background-position: center;
			background-size: 150px;
		}
	}
}

.attachment-info {

	.edit-attachment {
		display: none;
	}
}

.attachment-actions {

	.button.edit-attachment {
		display: none;
	}
}
